import React from 'react'

import { IntroductionContainer } from '../../styles/introductionStyles'
import { Container, DefaultSizes, VerticalPadding } from '../../styles/globalStyles'


const Introduction = () => {
    return(
        <Container>
            <IntroductionContainer>
                <VerticalPadding>
                    <DefaultSizes black>
                        <h1>Destaque-se.</h1>
                        <p>A sua marca, grande ou pequena, tem um superpoder. Tudo o que ela faz tem o potencial de criar boas experiências e sensações na mente dos seus clientes. Todas as ações, por mais pequenas que sejam, têm impacto nessa percepção. Assim, é importante que tais ações estejam cobertas com uma boa idêntidade. A mesma aparência, a mesma tonalidade e o mesmo tom de voz. Uma boa identidade, tem o poder de tornar a sua marca, algo especial aos olhos da sua audiência. Vamos criá-la juntos?</p>
                    </DefaultSizes>
                </VerticalPadding>
                
                
                
            </IntroductionContainer>
            
        </Container>
        
    )
}

export default Introduction;
